import React from "react";
import { motion, AnimatePresence } from "framer-motion";
import Router from "next/router";

import {
  toggleInnovationVideo,
  setCurrentSlideshowIndex,
  toggleSidebar,
  toggleNavbarSearch,
  toggleNavbarDownload,
  togglePhoneMenu
} from "../store/actions";
import { useSelector, useDispatch } from "react-redux";

import Link from "./Link";
import Img from "./Img";
import NavbarSearch from "./NavbarSearch";
// import PDFDownloadLink from './PDFDownloadLink';

import { DataContext } from "../helpers/dataContext";
import { useKeyPress } from "../hooks/useKeyPress";

const Navbar = ({ setScreen }) => {
  const { components, innovations, humans } = React.useContext(DataContext);
  const {
    activeInnovationId,
    showNavbarSearch,
    showNavbarDownload,
    showPhoneMenu
  } = useSelector(state => state);
  const [index, setIndex] = React.useState(0);
  // const [togglePhoneMenu, setPhoneMenu] = React.useState(false);
  const [showCopyPopup, toggleCopyPopup] = React.useState(false);
  const dispatch = useDispatch();
  const changeInnovationAction = changeInnovation(dispatch);
  const showContentsAction = showContents(dispatch);
  const { logo } = components.navbar;

  const handleInnovationChage = direction => {
    const nextPossiblePosition = index + direction;
    let nextPosition;
    if (nextPossiblePosition < 0) {
      nextPosition = 0;
    } else if (nextPossiblePosition >= innovations.length - 1) {
      nextPosition = innovations.length - 1;
    } else {
      nextPosition = nextPossiblePosition;
    }
    const nextActiveId = innovations[nextPosition].uid;
    changeInnovationAction(nextActiveId);
  };

  const copyToClipboard = str => {
    const el = document.createElement("textarea");
    el.value = str;
    document.body.appendChild(el);
    el.select();
    document.execCommand("copy");
    document.body.removeChild(el);
  };

  const leftArrow = useKeyPress("ArrowLeft");
  const rightArrow = useKeyPress("ArrowRight");

  /*                                              */
  /*    navigate between innovations with keys    */
  /*                                              */

  React.useEffect(() => {
    if (leftArrow) {
      handleInnovationChage(-1);
    } else if (rightArrow) {
      handleInnovationChage(1);
    }
  }, [leftArrow, rightArrow]);

  /*                                    */
  /*    set current index in toggler    */
  /*                                    */

  React.useEffect(() => {
    const currIndex = innovations.findIndex(
      innovation => innovation.uid === activeInnovationId
    );
    setIndex(currIndex);
  }, [activeInnovationId]);

  const formatedPos = pos => (pos <= 9 ? `0${pos}` : pos);

  const controlsVariants = {
    show: { opacity: 1, display: `flex` },
    hide: { opacity: 0, transitionEnd: { display: `none` } }
  };
  const showControls = activeInnovationId.length !== 0;

  return (
    <nav className={`navbar`}>
      <div className={`navbar__brand`}>
        <Img
          src={`/static/icons/${logo}`}
          onClick={() => {
            Router.push("/");
          }}
        />
      </div>

      {/*             */}
      {/*   toggler   */}
      {/*             */}

      <motion.div
        variants={controlsVariants}
        initial={false}
        animate={showControls ? `show` : `hide`}
        className={`navbar__toggler`}
      >
        <Img
          src={`/static/icons/minus.svg`}
          className={`navbar__toggler__btn`}
          onClick={() => handleInnovationChage(-1)}
        />
        <span className={`navbar__toggler__pos`}>{formatedPos(index + 1)}</span>
        <span className={`navbar__toggler__len`}>\ {innovations.length}</span>
        <Img
          src={`/static/icons/plus.svg`}
          className={`navbar__toggler__btn`}
          onClick={() => handleInnovationChage(1)}
        />
      </motion.div>

      {/*                */}
      {/*    controls    */}
      {/*                */}

      <motion.div
        variants={controlsVariants}
        animate={showControls ? `show` : `hide`}
        initial={false}
        className={`navbar__controls ${showPhoneMenu ? `show` : `hide`}`}
      >
        <div
          className={`navbar__controls__search`}
          onClick={() => {
            dispatch(toggleSidebar(`hide`));
            dispatch(toggleNavbarSearch(!showNavbarSearch));
          }}
        >
          <Img src={`/static/icons/search.svg`} />
          <span>Hledat</span>
        </div>
        {showNavbarSearch && <NavbarSearch />}

        {/*                        */}
        {/*    PDF downloadlink    */}
        {/*                        */}

        {/* <Img
          src={`/icons/download.svg`}
          onClick={() => {
            dispatch(toggleSidebar(`hide`));
            dispatch(toggleNavbarDownload(!showNavbarDownload));
          }}
        />
        <AnimatePresence initial={false}>
          {showNavbarDownload &&
            <PDFDownloadLink
              humans={humans}
              innovations={innovations}
              activeInnovationId={activeInnovationId}
              handleClick={bool => dispatch(toggleNavbarDownload(bool))}
            />
          }
        </AnimatePresence>*/}
        <div
          className={`navbar__controls__share`}
          onClick={() => {
            copyToClipboard(window.location.href);
            toggleCopyPopup(true);
          }}
        >
          <Img src={`/static/icons/share.svg`} />
          <span>Sdílet</span>
        </div>
        <AnimatePresence initial={false}>
          {showCopyPopup && (
            <CopyPopup onAnimationComplete={() => toggleCopyPopup(false)} />
          )}
        </AnimatePresence>
        <span
          className={`navbar__controls__contents`}
          onClick={() => {
            showContentsAction();
            dispatch(togglePhoneMenu(false));
          }}
        >
          Obsah
        </span>
      </motion.div>

      {/*              */}
      {/*    burger    */}
      {/*              */}

      <motion.div
        variants={controlsVariants}
        animate={showControls ? `show` : `hide`}
        initial={false}
        className={`navbar__burger`}
        onClick={() => dispatch(togglePhoneMenu(!showPhoneMenu))}
      >
        <Img className={``} src={`/static/icons/burger.svg`} />
      </motion.div>
    </nav>
  );
};

const CopyPopup = ({ ...props }) => {
  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0, transition: { delay: 1.5 } }}
      className={`popup`}
      {...props}
    >
      Odkaz zkopírován do schránky
    </motion.div>
  );
};

const showContents = dispatch => () => {
  dispatch(setCurrentSlideshowIndex(0));
  dispatch(toggleSidebar(`hide`));
  dispatch(toggleNavbarSearch(false));
  dispatch(toggleNavbarDownload(false));
  dispatch(toggleInnovationVideo(false));
  Router.push({ pathname: `/innovations` });
};

const changeInnovation = dispatch => nextActiveId => {
  // dispatch(setActiveInnovationId(nextActiveId));
  console.log(nextActiveId);
  dispatch(toggleInnovationVideo(false));
  dispatch(setCurrentSlideshowIndex(0));
  dispatch(toggleSidebar(`half`));
  dispatch(toggleNavbarSearch(false));
  dispatch(toggleNavbarDownload(false));
  Router.push({ pathname: `/innovations`, query: { id: nextActiveId } });
};

export default Navbar;
