import React from "react";
import { DataContext } from "../helpers/dataContext";
import { useDispatch } from "react-redux";
import { motion, AnimatePresence } from "framer-motion";
import Router from "next/router";

import {
  setActiveInnovationId,
  toggleNavbarSearch,
  toggleNavbarDownload,
  toggleSidebar,
  togglePhoneMenu
} from "../store/actions";

import Scrollbar from "./Scrollbar";
import Img from "./Img";

const listVariants = {
  initial: { opacity: 0, transition: { duration: 0.3 } },
  enter: { opacity: 1, transition: { duration: 0.3 } },
  exit: { opacity: 0, transition: { duration: 0.3 } }
};

const NavbarSearch = () => {
  const dispatch = useDispatch();
  const [query, updateQuery] = React.useState("");
  const [filteredItems, setFilteredItems] = React.useState([]);
  const { innovations, humans } = React.useContext(DataContext);

  React.useEffect(() => {
    if (query.length > 2) {
      const regex = new RegExp(query, "i");
      const testString = string => regex.test(string);
      const filterHumans = humans.filter(({ name }) => testString(name));
      const filterHumansIds = filterHumans.map(human => human.id);

      const matches = innovations.filter(
        ({ innovationname, about, authors }) => {
          const nameMatch = testString(innovationname[0].text);

          const aboutTextMatch = about.reduce((acc, curr) => {
            if (typeof curr.text === `string`) {
              return testString(curr.text) || acc;
            }
            return acc;
          }, false);

          const humanMatch = authors.reduce((acc, curr) => {
            return filterHumansIds.includes(curr.id) || acc;
          }, false);

          return nameMatch || aboutTextMatch || humanMatch;
        }
      );

      setFilteredItems(matches);
    } else {
      setFilteredItems([]);
    }
  }, [query]);

  return (
    <section className={`search`}>
      <input
        type="text"
        value={query}
        autoFocus={true}
        onChange={e => updateQuery(e.target.value)}
      />
      <AnimatePresence>
        {query.length > 2 && (
          <motion.div
            className={`search__list`}
            variants={listVariants}
            initial={`initial`}
            animate={`enter`}
            exit={`exit`}
          >
            <h1>Výsledky hledání</h1>
            <p>{`Počet výsledků: ${filteredItems.length}`}</p>
            <div
              className={`search__list__close`}
              onClick={() => dispatch(toggleNavbarSearch(false))}
            />
            {filteredItems.length > 0 ? (
              <Scrollbar vTrackStyle={{ right: 50, top: 0, left: `initial` }}>
                {filteredItems.map(({ innovationname, id, uid }) => (
                  <h3
                    onClick={() => {
                      updateQuery("");
                      dispatch(toggleSidebar(`half`));
                      dispatch(toggleNavbarSearch(false));
                      dispatch(toggleNavbarDownload(false));
                      dispatch(togglePhoneMenu(false));
                      Router.push({
                        pathname: "/innovations",
                        query: { id: uid }
                      });
                    }}
                    key={id}
                  >
                    {innovationname[0].text}
                  </h3>
                ))}
              </Scrollbar>
            ) : (
              <Img src={`/static/icons/sadCactus.svg`} />
            )}
          </motion.div>
        )}
      </AnimatePresence>
    </section>
  );
};

export default NavbarSearch;
